.ind-advantage {
  padding: 60px 0;
  width: 100%;
}

.ind-advantage .containers {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.ind-advantage__line {
  width: 72px;
  height: 1px;
  border-top: 1px solid #003f80;
}

.ind-advantage__title {
  margin: 35px 0 60px 0;
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
  line-height: 25px;
  text-align: center;
  color: #003f80;
}

.ind-advantage__table {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(376px, 1fr));
  width: 100%;
  justify-items: center;
}

.ind-advantage__table-items {
  width: 376px;
  background: #003f80;
  border-radius: 20px;
  height: max-content;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  padding: 45px 0 27px 0;
  position: relative;
}

.ind-advantage__table-icons {
  width: 280px;
  height: 280px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.ind-advantage__table-text {
  margin: 27px 0 35px 0;
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 23px;
  line-height: 31px;
  text-align: center;
  color: #ffffff;
}

.ind-advantage__table-btn {
  background: #003f80;
  border: 2px solid #ffffff;
  padding: 14px 50px;
  border-radius: 27px;
  font-family: Prata;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #ffffff;
  transition: all 0.5s ease;
}

.ind-advantage__table-btn:hover {
  transition: all 0.5s ease;
  background: #ffffff;
  color: #003f80;
}

.ind-advantage__table-left {
  position: absolute;
  bottom: 0;
  left: 0;
}

.ind-advantage__table-right {
  position: absolute;
  top: 0;
  right: 0;
}